import React from "react";
import PropTypes from "prop-types";
import "./sample-card.css";

import icon from "./icon-details.svg";

function SampleCard(props) {
  const { title, img, demoLink, repoLink } = props;

  let linkDemo = demoLink || repoLink || "";

  return (
    <div className="card sample-card">
      <a href={linkDemo} target="_blank" rel="noopener noreferrer">
        <div className="image">
          <img src={img} alt="Illustration" />
        </div>
        <div className="card-body">
          <h5 className="card-title">{title}</h5>
          <p className="card-text">
            <img className="icon-details" src={icon} alt="icon" />
            15 min · Web · Interface
          </p>
        </div>
      </a>
    </div>
  );
}

SampleCard.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string,
  img: PropTypes.string
};

export default SampleCard;
