import React from "react";
import "./samples-list.css";

import SampleCard from "../sample-card/sample-card";
import FilterInput from "../filter-input/filter-input";

import Fuse from "fuse.js";

class SamplesList extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      samples: props.samples
    };

    var fuseOptions = {
      threshold: 0.3,
      location: 0,
      distance: 100,
      maxPatternLength: 32,
      minMatchCharLength: 1,
      keys: ["name", "description"]
    };

    this.fuse = new Fuse(this.state.samples, fuseOptions);
  }

  handleFilterChange = query => {
    this.setState({
      filterQuery: query
    });
  };

  render() {
    let filteredSamples = this.state.samples;

    if (this.state.filterQuery) {
      filteredSamples = this.fuse.search(this.state.filterQuery);
    }

    return (
      <div className="container">
        <div className="filter">
          <FilterInput onChange={this.handleFilterChange} />
        </div>

        <div className="samples-list">
          {filteredSamples.map(sample => {
            return (
              <SampleCard
                key={sample.id}
                title={sample.name}
                description={sample.description}
                img={sample.img}
                repoLink={sample.repoLink}
                sandboxLink={sample.sandboxLink}
                demoLink={sample.demoLink}
              />
            );
          })}
        </div>
      </div>
    );
  }
}

export default SamplesList;
