import React from "react";
import { graphql } from "gatsby";

import Layout from "../components/layout";
import SEO from "../components/seo";
import Intro from "../components/intro/intro";
import SamplesList from "../components/samples-list/samples-list";

const IndexPage = ({ data }) => {
  let samples = data.allMarkdownRemark.edges.map(s => {
    var node = s.node;

    if (!node.frontmatter) {
      return {};
    }

    return {
      id: node.id,
      name: node.frontmatter.name,
      description: node.frontmatter.description,
      repoLink: node.frontmatter.repoLink,
      sandboxLink: node.frontmatter.sandboxLink,
      demoLink: node.frontmatter.demoLink,
      img: node.frontmatter.image ? node.frontmatter.image.publicURL : ""
    };
  });

  return (
    <Layout>
      <SEO
        title="Samples"
        keywords={[`stripe`, `samples`, `recipies`]}
        description="Pre-built blocks to get started on Stripe."
      />

      <Intro />

      <SamplesList samples={samples} />
    </Layout>
  );
};

export default IndexPage;

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { glob: "**/content/samples/**/*" } }
    ) {
      edges {
        node {
          id
          frontmatter {
            name
            description
            sandboxLink
            repoLink
            demoLink
            image {
              publicURL
            }
          }
        }
      }
    }
  }
`;
